  margin-right: auto;
  text-decoration: none;
  color: #F9F6F2;
  background-color: #86B2B3;
  width: 8vw;
  height: 4vw;
  text-align: center;
  font-weight: 600;
  margin-top: 1vw;
  line-height: 3vw;
  font-size: 1.3vw;
  border-radius: 15px;
  border-color: #86B2B3;
  border-style: solid;
  border-width: 0.5vw;
}

.startBtn:hover {
  background-color: #7ba4a4;
}

